body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

div.p-datatable.p-component.p-datatable-scrollable > div.p-datatable-scrollable-wrapper > div > div.p-datatable-scrollable-body {
  height: 340px !important;
}

button.p-paginator-next.p-paginator-element.p-link > span , 
button.p-paginator-last.p-paginator-element.p-link > span , 
button.p-paginator-prev.p-paginator-element.p-link > span , 
button.p-paginator-first.p-paginator-element.p-link > span 
{
  font-size: .7rem;
}

button.p-paginator-page.p-paginator-element.p-link {
  font-size: 12px;
  height: 2.7em !important;
}

.p-growl-image {
  align-self: center !important;
  font-size: 20px !important;
  font-weight: bold !important;
}

.p-growl-icon-close-icon {
  font-size: 0.8em !important;
}

span.p-submenu-icon.pi.pi-fw.pi-caret-right {
  font-size: .7rem;
}

.numerictext {
  text-align: right !important;
}

.form-row {
  padding-left: 8px;
  padding-right: 8px;
}

.lb {
  font-size: 14px;
}

.btnicon {
  margin-bottom: 4px;
}

.lb-validate {
  color: red;
}

.nextitem {
  margin-left: 5px;
}

div.dropdownround > div.ant-select-selector {
  border-radius: 50px !important;
  padding-left: 5px;
}

div.dropdownround > span.ant-select-arrow {
  top: 60% !important;
}

div.formtabnormal > .ant-tabs-nav > .ant-tabs-nav-wrap > .ant-tabs-nav-list {
  height: 35px !important;
}

div.formtabnormal > .ant-tabs-nav > .ant-tabs-nav-wrap > .ant-tabs-nav-list > .ant-tabs-tab {
  position: relative;
  top: 3px;
  font-size: 15px;
  padding-left: 25px;
  padding-right: 30px;
}

div.formtabnormal > .ant-tabs-nav > .ant-tabs-nav-wrap > .ant-tabs-nav-list > .ant-tabs-tab-active > .ant-tabs-tab-btn {
  color: black !important;
}

div.formtabnormal > .ant-tabs-nav > .ant-tabs-nav-wrap > .ant-tabs-nav-list > .ant-tabs-ink-bar {
  background-color: #75FA4C;
  height: 3px;
}

span > svg {
  position: relative;
  top: -3px;
}

.hr-sect {
	display: flex;
	flex-basis: 100%;
	align-items: center;
	font-size: 12px;
  margin: 8px 0px;
  font-weight: bold;
  font-size: 1rem;
}

.hr-sect::after {
	content: "";
	flex-grow: 1;
	background: #C8C8C8;
	height: 1px;
	font-size: 0px;
  line-height: 0px;
  margin-left: 10px;
}

/* Let's get this party started */
::-webkit-scrollbar {
  width: 8px;
  height: 12px;
}

/* Track */
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
  -webkit-border-radius: 10px;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  -webkit-border-radius: 10px;
  border-radius: 10px;
  background: #E9ECEF;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}
::-webkit-scrollbar-thumb:window-inactive {
background: #E9ECEF
}

